import { useState, useRef } from 'react';
import classnames from 'classnames';
import { Slider } from 'antd';

const Ambience = (props) => {
  const [enable, setEnable] = useState(false);
  const ref = useRef();

  console.log(ref.current);

  return (
    <>
      <audio
        ref={(a) => (ref = a)}
        src={props.src}
        loop
        muted={!enable}
        autoPlay
      />
      <div
        className={`flex items-center pt-8 cursor-pointer select-none ${
          enable ? 'text-secondary' : 'text-grey'
        }`}
        onClick={() => {
          setEnable(!enable);
        }}
      >
        <props.icon className={classnames(props.iconClass)} />
        {props.name}
      </div>
      <Slider disabled={!enable} value={ref.current?.getVolume * 100}></Slider>
    </>
  );
};

export default Ambience;
